AOS.init({
    duration: 1300,
    anchorPlacement: "top-center",
});

$(document).ready(function() {

    $(".callback__form").on("submit", function (event) {
        var $that = $(this);
        var $formData = new FormData($that.get(0));
        $.ajax({
          url: $that.attr("action"),
          type: $that.attr("method"),
          contentType: false,
          processData: false,
          data: $formData,
          dataType: "text",
          success: function success(data) {
            $('.popup-ty').fadeIn();
            setTimeout(function () {
              $('.popup-ty').fadeOut();
              $('.popup-overlay').fadeOut();
            }, 3000);
          }
        });
        event.preventDefault();
    });

    if(window.innerWidth <= 768) {

        var infoType = $('.ships__item__info p.info__name');
        $(infoType).each(function(index, element) {
            $(element).prependTo($(element).closest('.ships__item'));
        });
        var infoType = $('.ships__item__info p.info__type');
        $(infoType).each(function(index, element) {
            $(element).prependTo($(element).closest('.ships__item'));
        });
    }
    
})


